<template>
  <div class="servicerecordadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
           <v-row>
             <v-col cols="12" sm="2">
               <v-text-field label="評估日期" v-model="list.date" type="date" :rules="emptyRules"></v-text-field>
             </v-col>
             <v-col cols="12" sm="2">
               <v-select :items="targetItems" label="服務對象" v-model="list.target"></v-select>
             </v-col>
             <v-col cols="12" sm="2">
               <v-select :items="typeItems" label="服務類型" v-model="list.type"></v-select>
             </v-col>
             <v-col cols="12" sm="3">
               <v-select :items="locationItems" label="服務地點" v-model="list.location"></v-select>
             </v-col>
             <v-col cols="12" sm="3">
               <v-select :items="modelItems" label="服務模式" v-model="list.model"></v-select>
             </v-col>
             <v-col cols="12">
               <v-textarea label="近況簡述" v-model="list.simplecontent"></v-textarea>
             </v-col>
             <v-col cols="12">
               <v-textarea label="下次計畫" v-model="list.nextplan"></v-textarea>
             </v-col>
             
            </v-row> 
           
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
         <!-- <pre>{{list}}</pre>  -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      targetItems:['個案本人','個案親友','其他'],
      typeItems:['定期訪視','疾病狀況','家庭關係','意見申訴','情緒問題','福利服務','意外傷害','個案研討','其他'],
      locationItems:['機構內','個案家中','意見申訴','醫療院所','其他'],
      modelItems:['直接服務','照會其他專業','通報機構主管','轉介其他單位'],
      loading:false,
    };
  },
  props: ["edititem", "viewData"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function() {
      this.dialog = true;
      this.editProcess();
    });
  },
  model: {
    prop: "edititem"
    // event: "parentData"
  },

  watch: {
    edititem: [
      {
        handler: "editProcess"
      }
    ]
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      this.list = { ...this.viewData[0] };
      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess() {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = this.edititem; //將頁面資料與父項的資料連結
      this.list = { ...this.edititem }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/servicerecord",
            qs.stringify(this.list)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/servicerecord",
            qs.stringify(this.list)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      }else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    }
  }
};
</script>
